import { _decorator, Component, Node,log } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('HelloWorld')
export class HelloWorld extends Component {
    start() {
        let studentID: number = 2202100471;
        let studentIDArray: number[] = [studentID];

    for (let i = 0; i < studentIDArray.length; i++) {
        switch (studentIDArray[i]) {
            case 1:
            case 3:
            case 5:
            case 7:
                if (studentIDArray[i] % 2 === 1) {
                // 相加
                studentIDArray[i + 1] = studentIDArray[i] + 1;
                } 
                else {
                // 相减
                studentIDArray[i + 1] = studentIDArray[i] - 1;
                }
                break;
            case 2:
            case 4:
            case 6:
                if (studentIDArray[i] % 2 === 0) {
                    // 相加
                    studentIDArray[i + 1] = studentIDArray[i] + 1;
                } else {
                    // 相减
                    studentIDArray[i + 1] = studentIDArray[i] - 1;
                }
                break;
            default:
            // 其他情况不进行操作
                break;
            }
            if (studentIDArray[i + 1] > 10 || studentIDArray[i + 1] < -10) {
            break;
        }
        console.log(studentIDArray[i + 1]);
    }
  }

}



